<template>
    <div class="login-box">
		<div class="black-bj"></div>
		<div class="content">
			<div class="logo"></div>
			<div class="input-box">
				<input class="phone" type="tel" name="" id="" placeholder="手机号">
				<div class="icon icon1"></div>
			</div>
			<div class="input-box">
				<input class="pass" type="password" name="" id="" placeholder="密码">
				<div class="icon icon2"></div>
			</div>
			<div class="input-box">
				<input class="code" type="tel" name="" id="" placeholder="验证码">
				<div class="yzm">获取验证码</div>
			</div>
			<a href="javascript:;" class="btn-login">立即登录</a>
			<p class="bottom-p">
				<label><input class="remember" type="checkbox" name="" id="" />
				记住账号</label><span> | <a href="">忘记密码</a></span>
			</p>
		</div>
		<div class="close"></div>
	</div>
</template>

<script>
export default {
  name: 'login',
  data () {
    return {
      
    }
  },
  mounted () {
    /*登录*/
    $(document).on('click','.header .btn-login,.nav .btn-login',function(){
        $('body').find('.app-other,.app-index').addClass('filter');
        $('.login-box').removeClass('filter').fadeIn(200);
    });
    $('.login-box').on('click','.close',function(){
        $('.login-box').fadeOut(200);
        $('body').find('.app-other,.app-index').removeClass('filter');
    });
    /*点击验证码*/
    $('.login-box').on('click','.yzm',function(){
        var $phone=$('.login-box .phone');
        if(!/1[3-8][0-9]{9}/.test($phone.val())){
            alert('请输入正确手机号码');
            $phone.focus().select();
        }else{
            /*验证通过*/
        }
    });
    /*点击立即登录*/
    $('.login-box').on('click','.btn-login',function(){
        var $phone=$('.login-box .phone'),
            $pass=$('.login-box .pass'),
            $code=$('.login-box .code'),
            $remember=$('.login-box .remember');
        if(!$phone.val()){
            alert('输入手机号码不能为空！');
            $phone.focus();
            return;
        }    
        if(!$pass.val()){
            alert('输入密码不能为空！');
            $pass.focus();
            return;
        }else if($pass.val().length<7){
            alert('输入密码不少于6位！');
            $pass.focus().select();
            return;
        }
        if(!$code.val()){
            alert('输入验证码不能为空！');
            $code.focus();
            return;
        }
        /*验证通过*/
        if($remember.prop('checked')){
            /*保存cookie*/
            var ExpireDate = new Date ();
            ExpireDate.setTime(ExpireDate.getTime() + (7 * 24 * 3600 * 1000));
            document.cookie='phone='+$phone.val()+'; expires=' + ExpireDate.toGMTString();
        }else{
            var ExpireDate = new Date ();
            ExpireDate.setTime(ExpireDate.getTime()-1);
            document.cookie='phone='+$phone.val()+'; expires=' + ExpireDate.toGMTString();
        }
    });
  }
}
</script>

