  },
  mounted () {
    /*提交审核*/
    $('.affiliate-box5').on('click','.btn-submit',function(){
        var $form=$('.affiliate-box5 form'),
            $input=$('.affiliate-box5 input'),
            $select=$('.affiliate-box5 select'),
            boolpass=true,error='',radiopass=false;
        $.map($('.label-radio input'),function(html){
            var $this=$(html);
            if($this.prop('checked')) radiopass=true;
        }); 
        if(!radiopass){
            boolpass=false;
            error="请选择回访时间";
        }

        $.map($select,function(html){
            var $this=$(html);
            if(!$this.val()){
                boolpass=false;
                error=$this.attr('data-tip');
                $this.focus();
                return false;
            }
        }); 
        $.map($input,function(html){
            var $this=$(html);
            if(!$this.val()){
                boolpass=false;
                error=$this.attr('data-tip');
                $this.focus();
                return false;
            }else if($this.prop('type')=='tel'){
                if(!/1[3-8][0-9]{9}/.test($this.val())){
                    boolpass=false;
                    error='请输入正确手机号码';
                    $this.focus();
                    return false;
                }
            }
        });
        if(boolpass){
            /*验证通过*/
            console.log('验证通过');
        }else{
            alert(error);
        }
    });

    /*选择地区*/
    $('select').on('change',function(){
        var $this=$(this);
        if($this.val()){
            $this.removeClass('no-select');
            $this.parent('.box').find('.tip').fadeOut(400);
        }else{
            $this.addClass('no-select');
        }
    });

    /*点击立即加盟*/
    $('.affiliate-banner').on('click','a',function(){
        var $aff5=$('.affiliate-box5').offset().top;
        $('html,body').animate({
            'scrollTop': $aff5
        },600);
    });
  }
}
</script>

