<script>
export default {
  name: 'news',
  data () {
    return {
      
    }
  },
  mounted () {
    /*延迟加载图片*/
    if($('[data-src]').length>0){
        var $pic=$('[data-src]'),
            count=$pic.length,
            imgLoad=function(index){
                var img=new Image(),
                    str_src=$pic.eq(index).attr('data-src');
                img.src=str_src;
                img.onload=function(){
                    $pic.eq(index).css({
                        opacity: '1',
                        backgroundImage: 'url(\''+img.src+'\')'
                    });
                    count--;
                    if(count>0){
                        imgLoad($pic.length-count);
                    }
                }
            };
        imgLoad($pic.length-count);
    }
  }
}
</script>

