<template><div class="app-other">
    <div class="header">
        <div class="wrapper" style="width: 95%;">
            <router-link class="logo" to="/"></router-link>
            <ul class="dh">
                <li><router-link to="/">首页</router-link></li>
                <li name="product"><router-link to="/product">运营产品</router-link></li>
                <li name="news"><router-link to="/news">新闻中心</router-link></li>
                <li name="affiliate"><router-link to="/affiliate">申请加盟</router-link></li>
                <li name="about"><router-link to="/about">关于我们</router-link></li>
            </ul>
            <a href="javascript:;" class="btn-login">登录</a>
        </div>
    </div>
    <transition name="zoom">
        <router-view></router-view>
    </transition>
    <div class="footer">
        <p class="title">北京壹千克网络科技有限公司 版权所有 2015-2016 ICP证：京20150087<span>客服热线</span> 4000-844-111</p>
    </div>
</div></template>

<script>
export default {
  name: 'other',
  data () {
    return {
      str_route: this.$route.name==='newsDetail'?'news':this.$route.name
    }
  },
  watch:{
      $route(to, from){
        this.str_route=to.name==='newsDetail'?'news':to.name;
        let $li=$('.header .dh>li[name='+this.str_route+']'),
            li_prev_count=$li.prevAll().length;
        $('.header .dh').css('backgroundPosition',li_prev_count*100+'px 0px');
        $li.addClass('active');
      }
  },
  mounted () {
      $('.right-fixed').fadeIn();
      let _this=this;
    $('html,body').removeAttr('class style');
    let $li=$('.header .dh>li[name='+this.str_route+']'),
        li_prev_count=$li.prevAll().length;
    $('.header .dh').css('backgroundPosition',li_prev_count*100+'px 0px');
    $li.addClass('active');
    /*移过导航*/
    $('.header .dh>li').hover(function(){
        let $this=$(this),
            cur_num=$this.prevAll().length;
        $('.header .dh').css({
            'background-position': (cur_num*100)+'px 0'
        });    
        $this.addClass('active').siblings().removeClass('active');
    },function(){
        let $li=$('.header .dh>li[name='+_this.str_route+']'),
            li_prev_count=$li.prevAll().length;
        $('.header .dh').css({
            'background-position': li_prev_count*100+'px 0'
        });
        $('.header .dh>li').removeClass('active');
        $li.addClass('active');
    });

    /*滚动侦听，导航*/
    $(window).on('scroll',function(){
        var s_top=$(window).scrollTop();
        if(s_top>=100){
            $('.header').css({
                'transform' : 'translate(0,-100px)',
                '-webkit-transform' : 'translate(0,-100px)',
                '-moz-transform' : 'translate(0,-100px)',
                '-o-transform' : 'translate(0,-100px)'
            });
        }else{
            $('.header').css({
                'transform' : 'translate(0,0)',
                '-webkit-transform' : 'translate(0,0)',
                '-moz-transform' : 'translate(0,0)',
                '-o-transform' : 'translate(0,0)'
            });
        }
    });
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.zoom-enter-active {
  animation: fadeIn .4s ease .2s;
}
.zoom-leave-active {
  animation: fadeOut .2s;
}
.app-other{
    background-color: #f6f6f6;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
}
.header{
    height: 100px;
    position: relative;
    margin: 0 auto;
    background-color: #ffffff;
    min-width: 990px;
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    transition: all 1s;
    -webkit-transition: all 1s;
    -moz-transition: all 1s;
    z-index: 9;
}
.header .logo{
    height: 55px;
    width: 316px;
    background-image: url('../assets/images/logo1.png');
    display: inline-block;
    float: left;
    margin-top: 24px;
}
.header .dh{
    height: 35px;
    float: left;
    margin-top: 34px;
    margin-left: 35px;
    background-image: url('../assets/images/dh_selected_1.png');
    background-repeat: no-repeat;
    -webkit-transition: all 0.6s;
    transition: all 0.6s;
}
.header .dh>li{
    width: 100px;
    height: 35px;
    float: left;
    text-align: center;
    line-height: 35px;
    position: relative;
    font-size: 16px;
}
.header .dh>li.active>a{
    color: #e4392a;
}
.header .dh>li>a{
    color: #333333;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    display: inline-block;
    z-index: 2;
    -webkit-transition: all 0.8s;
    transition: all 0.8s;
}
.header .btn-login{
    display: inline-block;
    width: 100px;
    height: 35px;
    line-height: 35px;
    background-color: #e4392a;
    font-size: 16px;
    text-align: center;
    border-radius: 30px;
    color: #ffffff;
    position: absolute;
    top: 36px;
    right: 0;
    -webkit-transition: all 0.4s;
    transition: all 0.4s;
}
.header .btn-login:hover{
    background-color: #cc3325;
}
.wrapper.w1140{
    width: 100%;
    max-width: 1140px;
    margin: 40px auto 80px;
}
</style>



