<template><div>
    <div class="wrapper w1140" style="margin-top: 140px;">
        <div class="news-detail">
            <h1>小额贷款保证险再拓新平台</h1>
            <div class="title">来源：张家口日报　我有话说</div>
            <p>武汉一直都是一个有争议的城市，在爱她的人眼里她像个性热情又活泼、刚烈，爱恨分明的女子，而不爱的她的人则会觉得她是一个爱挑剔的暴躁小姐。的确，作为三大火炉之一的武汉，如果这座城市没有脾气那么谁会爱上她呢？谁又会记住她呢？当然对于我来说爱更多一些，最难忘的非火辣辣的武汉美食莫属！</p>
            <p>来到武汉生活了四余年，首要就是像武汉人一样会吃鸭脖。在这片荆楚大地上，环顾四周，你就不难发现各式各样的鸭脖鸡爪店，比如武汉人的骄傲周黑鸭、四川的廖记棒棒鸡、绝味、荆州的小胡鸭、紫燕百味鸡...当然还有很多名气没有那么大的小店。</p>
            <p>而且像周黑鸭、棒棒鸡、绝味、小胡鸭都是大概走十步就可以看到的，想吃绝对是分分钟的问题。</p>
            <p>周黑鸭的每一样都不会太让人失望，不过我最喜欢的还是是鸭脖、鸭锁骨、鸡爪、鱿鱼、鸭舌、藕片和卤干子。而且来武汉吃周黑鸭应该是最“实惠”的。有去深圳和北京等地买过，价格都比武汉要贵些。毕竟他们是空运到除了武汉以外的城市，所以会在成本上加上路费！</p>
            <p>我是廖记棒棒鸡的忠实脑残粉，最爱的是它家的夫妻肺片，而且很多不能吃辣的小伙伴也可以尝试，因为他们的佐料是可以按个人的喜好来改变的，非常灵活。拌好后加上花生香菜和淋上火辣辣的辣椒油，简直是味蕾盛宴，而且可以单吃也可以配饭，非常好搭配。唯一的缺点是因为他家是现场拌汁，所以不能放很久，泡久了就失去了该有的嚼劲和香味了，肉质也会变烂很多。当然他家也有鸡翅、鸡爪...藕片也不错，做法和味道都更偏四川，以麻、香为主，所以相比对鸡和鸭的处理，我还是更偏于周黑鸭。</p>
            <p>至于绝味，毕竟是全国连锁，所以味道也差不到哪里去。</p>
        </div>
    </div>
    <router-link class="news-detail-back" to="/news">返回</router-link>
</div></template>

<script>
export default {
  name: 'newsDetail',
  data () {
    return {
      
    }
  },
  mounted () {
    $('.right-fixed').fadeOut();
    /*显示返回*/
    $('.news-detail-back').animate({
        right: '64px',
        opacity: '1'
    },800);
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
/* 新闻详情 */
.news-detail{
    background-color: #ffffff;
    box-sizing: border-box;
    padding: 40px 40px;
    min-height: 800px;
}
.news-detail h1{
    font-size: 22px;
    font-weight: normal;
    color: #333333;
    letter-spacing: 1px;
}
.news-detail .title{
    color: #666666;
    font-size: 12px;
    margin: 10px 0 50px;
}
.news-detail p{
    line-height: 24px;
    font-size: 14px;
    color: #333333;
    margin: 28px 0;
    letter-spacing: 1px;
}
.news-detail-back{
    position: fixed;
    width: 52px;
    height: 52px;
    bottom: 150px;
    right: -55px;
    background-color: transparent;
    text-align: center;
    line-height: 55px;
    font-size: 14px;
    color: #e4392a;
    display: inline-block;
    cursor: pointer;
    -webkit-transition: all 0.4s;
    transition: all 0.4s;
    opacity: 0;
    border: 2px solid #e4392a;
    border-radius: 54px;
}
.news-detail-back:hover{
    background-color: #e4392a;
    color: #ffffff;
}
</style>



